@import url(https://fonts.googleapis.com/css?family=Muli);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.koshurvagabond-form-FB-button{


    border-radius: 30px;
    border: none;
    text-align: center;
    font-size: 1.5em;
    font-weight: 600;
    width: 330px;
    height: 60px;
    padding: 10px;

    opacity: 0.5;
    background-color: #bcd0c7 !important;
    border-color: #bcd0c7 !important;
    color: #2f3e46 !important;
} 
.koshurvagabond-views-login-form{

    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 80%;
    max-width: 80%;

   
}

.koshurvagabond-views-formitem{

    margin-top: 5%;
    text-align: left;
}

.koshurvagabond-views-login-btn{

    margin-top: 5%;
}

.koshurvagabond-views-login-socialmedia{

    margin-top: 10%;
}

.form-box{

    display: flex;
    flex-direction: column;
    align-items: flex-end; 
}

.form-box a{

    font-size: 1.3em;
    margin-top: 10px;
    color: #2f3e46;
}

.koshurvagabond-views-signup-form{

    display: flex;
    flex-direction: column;
    
    align-items: center;
    
}

.koshurvagabond-views-formitem{

    margin-top: 5%;
    height: 30px;
    text-align: left;
}


.koshurvagabond-views-signup-btn{

    margin-top: 5%;
}

*{
    font-family: 'Muli', sans-serif;
}

.app-container{

    display: flex;
    flex-direction: column;
    background-color: #effbf0;
    align-items: center;
    justify-content: center;
    height: 100%;

    position:fixed;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
}

.koshurvagabond-form-input{

    border-radius: 30px;
    height: 30px;
    padding: 10px;
    font-size: 1.2em;
    border: solid 2px #0a5960;   
    width:300px;
    
     
}

.koshurvagabond-form-btn{

    border-radius: 30px;
    background-color: #50ce52;
    border: none;
    color: #ffffff;
    text-align: center;
    font-size: 1.5em;
    width: 330px;
    height: 50px;
    padding: 0;
}

.koshurvagabond-title-text{

    color: #50ce52;
    font-size: 40px;
    margin: 0;
    padding: 0;
}

.koshurvagabond-link{

    font-size: 20px;
}

.koshurvagabond-views-loading-container{

    display: flex;
    flex-direction: column;
    height: 100%;

    position:fixed;
    top:0px;
    bottom:0px;
    left:0px;
    right:0px;
    background-image:url(../../static/media/loading.5cdf9287.jpg);
    background-repeat: no-repeat;
    background-size: 100% 100%;
}

